
.header {
  color: #666;
  background: #fff;
  height: 2.5rem;
  font-size: 0.5rem;
}
.header .center {
  width: 25rem;
  height: 100%;
  margin: 0 auto;
  background: #fff url(../../images/ionic-favicon.png) left center no-repeat;
  background-size: auto 50%;
  position: relative;
}
.header .center .navbar {
  position: absolute;
  left: 3.75rem;
  top: 50%;
  transform: translate(0, -50%);
}
.header .center .navbar span {
  float: left;
  margin-left: 0.75rem;
}
.header .center .message {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  right: 0.5rem;
  width: 5rem;
}
.header .center .message img {
  width: 1rem;
  height: 1rem;
}
.header .center .message em {
  position: absolute;
  left: 1.5rem;
  top: 50%;
  transform: translate(0, -50%);
}